.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white slick-image-url("ajax-loader.gif") center center no-repeat; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; top: 50%; margin-top: -14px; font: 0/0 a; text-shadow: none; color: transparent; height: 50px; background: none; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 50px; line-height: 50px; }

.slick-prev { left: 0; }
.slick-prev:before { content: "←"; display: inline-block; vertical-align: middle; font-size: 61px; color: #fff; }

.slick-next { right: 0; }
.slick-next:before { content: "→"; display: inline-block; vertical-align: middle; font-size: 61px; color: #fff; }

.slick-dots { text-align: center; position: absolute; margin: 0; bottom: 0; left: 0; right: 0; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { opacity: 0.3; box-shadow: none; }
.slick-dots li:before { display: none; }
.slick-dots button { display: block; width: 6px; height: 6px; background: #fff; box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25); cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 50%; }
.slick-dots button:hover { box-shadow: none; }

@font-face { font-family: "Roboto Light"; src: url("fonts/Roboto-Light.eot"); src: url("fonts/Roboto-Light.eot?#iefix") format("embedded-opentype"), url("fonts/Roboto-Light.woff2") format("woff2"), url("fonts/Roboto-Light.woff") format("woff"), url("fonts/Roboto-Light.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.in, .l, .header__in { margin: 0 auto; padding: 0 10px; max-width: 1200px; *zoom: 1; }
.in:after, .l:after, .header__in:after { content: " "; display: table; clear: both; }

body { font-family: "Roboto Light", Arial, sans-serif; font-size: 14px; line-height: 1.7; font-size: 14px; background: #fafbfc; }

a { text-decoration: none; }

.l { *zoom: 1; }
.l:after { content: " "; display: table; clear: both; }
.l__in { padding: 20px 0 25px; }

.l-col1 { float: left; margin-right: 1.65%; width: 23.81%; }

.l-col2 { float: right; width: 23.7%; }

.center { float: left; width: 49.2%; }

.header { height: 50px; background: #263339; }

.logo { float: left; padding-left: 5px; width: 70px; line-height: 50px; display: inline-block; vertical-align: top; }
.logo .icon { vertical-align: middle; }

.menu { float: left; font-size: 0; }
.menu a { padding: 0 18px; color: #fff; height: 50px; line-height: 50px; font-size: 14px; transition: background 0.2s; border-right: 1px solid #38474f; display: inline-block; }
.menu a:hover, .menu a.is-active { background: #546d79; }
.menu a:first-child { border-left: 1px solid #38474f; }

.user { float: right; transition: background 0.2s; }
.user:hover, .user.is-active { background: #546d79; }
.user__in { position: relative; height: 50px; color: #fff; line-height: 50px; border-left: 1px solid #38474f; display: block; }
.user__name { padding: 0 15px 0 35px; display: block; }
.user .icon { position: absolute; left: 12px; top: 50%; margin-top: calc(-19px/2); font-size: 19px; }

.wind { overflow: hidden; box-shadow: 0 0 5px rgba(84, 109, 121, 0.1); border-radius: 2px; background: #fff; }
.wind + .wind { margin-top: 20px; }
.wind__top { position: relative; padding: 10px; color: #fff; line-height: 1.45; background: #546d79; }
.wind__top .btn { position: absolute; top: 0; right: 0; }

.item { padding: 8px 10px; border-top: 1px solid #edeff1; display: block; *zoom: 1; }
.item:after { content: " "; display: table; clear: both; }
.item:first-child { border-top: none; }
.item__in { *zoom: 1; }
.item__in:after { content: " "; display: table; clear: both; }
.item__left { float: left; max-width: 140px; }
.item__right { float: right; text-align: right; max-width: 140px; }
.item__stat { margin-top: 7px; padding-bottom: 2px; text-align: center; }
.item .sum + .sum { margin-top: 3px; }

a.item { color: #263238; }

.sum.is-queue { color: #90a4ae; }
.sum.is-queue .sum__curr { color: #90a4ae; }
.sum__bal { vertical-align: top; display: inline-block; }
.sum__res { color: #546e7a; }
.sum__curr { color: #b0bec5; }

.stat { padding: 0 7px; line-height: 1.4; vertical-align: top; border-radius: 1px; border: 1px solid #546e7a; display: inline-block; }

.btn { padding: 13px 20px; color: #fff; font-family: "Roboto Light", Arial, sans-serif; font-size: 14px; line-height: 1; transition: background 0.2s; background: #9ccc65; display: inline-block; }
.btn:hover { background: #699833; }
.btn:active { background: #4f7226; }
.btn_blue { background: #42a5f5; }
.btn_blue:hover { background: #0b72c6; }
.btn_blue:active { background: #085796; }

.btn-text { position: relative; left: 50%; transform: translate(-50%, 0); color: #607d8b; display: inline-block; }
.btn-text:hover span { border-color: #607d8b; }
.btn-text span { line-height: 1.3; transition: border-color 0.2s; border-bottom: 1px solid transparent; display: inline-block; }

.card__numb, .card__img { display: inline-block; }
.card__numb { vertical-align: top; }
.card__img { position: relative; max-width: 33px; font-size: 0; }
.card__img img { width: 100%; display: block; }

.slider { position: relative; color: #fff; background: #64b5f6; }
.slider__close { position: absolute; top: 13px; right: 10px; color: #fff; font-size: 20px; z-index: 1; opacity: 0.5; transition: opacity 0.2s; background: transparent; }
.slider__close:hover { opacity: 1; }
.slider__slide { width: 100%; }
.slider__in { padding: 10px 10px 15px; }
.slider__title { margin-bottom: 20px; }
.slider__img { max-height: 150px; font-size: 0; text-align: center; }
.slider__img img { display: inline-block; height: 100%; }
.slider__text { margin-top: 20px; }
.slider .btn { width: 100%; text-align: center; }

.arrows { position: relative; }
.arrows .arrow { position: relative; z-index: 30; }


